@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap");

/* font-family: 'Montserrat', sans-serif; */

* {
  margin: 0;
  padding: 0;
}
body {
  background: #fff;
  color: #222;
  font-family: "Montserrat", sans-serif;
  font-family: 300;
}

.wrapper {
  width: 1280px;
  margin: 50px auto;
}
heade {
  position: relative;
}
header .logo {
  font-weight: 600;
  font-size: 20px;
}

header ul.nav {
  float: right;
  list-style: none;
}

header ul.nav li {
  display: inline;
  margin-left: 25px;
  cursor: pointer;
  transition: opacity 500ms ease;
}

header ul.nav li:hover {
  opacity: 0.5;
  text-shadow: 1px 1px 1px #a3927c;
}
header ul.nav li:active {
  position: relative;
  top: 1px;
}
header .shop-cart-button {
  float: right;
  cursor: pointer;
  transition: color, transform 500ms ease;
}
header .shop-cart-button:hover,
header .shop-cart-button.active {
  color: #dc3d3d;
  transform: scale(1.5);
}

header .shop-cart {
  position: absolute;
  top: 80px;
  right: 0;
  width: 450px;

  background: #fafafa;
  box-shadow: 4px 4px 15px -7px #606060;
  z-index: 1;

  padding: 20px;
  padding-bottom: 0;
}

header .shop-cart .item {
  width: 100%;
  float: left;
  margin-bottom: 20px;
}
header .shop-cart .item h2 {
  font-size: 20px;
  margin-bottom: 10px;
}
header .shop-cart .item p {
  color: #797979;
  font-weight: 600;
}

header .shop-cart .item img {
  width: 70px;
  float: left;
  margin-right: 20px;
}
header .shop-cart .empty h2 {
  font-size: 20px;
  margin-bottom: 20px;
}
header .shop-cart .item .delet-icon {
  color: red;
  float: right;
  position: relative;
  top: -25px;
  cursor: pointer;
  transition: color, transform 300ms ease;
}

header .shop-cart .item .delet-icon:hover {
  color: #ca5252;
  transform: scale(1.5);
}

header .presentation {
  margin: 50px 0;
  background: url("./img/bg.jpg") no-repeat;
  width: 100%;
  height: 500px;
  background-size: cover;
  background-position: center -250px;
  background-blend-mode: multiply;
  background-color: #bcbcbc;
  position: relative;
}

header .presentation::after {
  content: "Лучшие товары для вашего дома";
  position: absolute;
  top: 100px;
  left: 196px;
  width: 300px;
  font-size: 40px;
  font-weight: 600;
  opacity: 0.75;
}
header .presentation::before {
  content: "дорого и качественно";
  position: absolute;
  top: 250px;
  left: 196px;
  width: 300px;
  font-size: 15px;
  font-weight: 300;
  opacity: 0.75;
  color: #7f3541;
}

header .shop-cart .summa {
  float: left;
  width: 100%;
  font-weight: 600;
  font-size: 20px;
  margin-bottom: 20px;
}
/* Categories  */
.categories div {
  display: inline-block;
  background: #f2f2f2;
  border-radius: 50px;
  padding: 10px 20px;
  margin-bottom: 25px;
  margin-right: 15px;
  cursor: pointer;
  border: 1px solid transparent;
  transition: all 500ms ease;
}
.categories div:hover {
  border-color: silver;
  transform: scale(1.1);
}

main {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
}

main .item {
  width: 30%;
  padding-bottom: 15px;
  margin-bottom: 50px;
  background: #f5f5f5;
  overflow: hidden;
  border-radius: 10px 10px 0 0;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.4);
  position: relative;
}

main .item img,
.full-item img {
  width: 100%;
  border-radius: 10px 10px 0 0;
  transition: transform 500ms ease-in;
  cursor: pointer;
}
main .item img:hover,
.full-item img:hover {
  transform: scale(1.01);
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.4);
}
main h2,
.full-item h2,
main p,
.full-item p,
main b,
.full-item b,
.full-item b {
  margin: 10px 20px;
  color: #333;
}
main b {
  color: #5fa36a;
}

main .add-to-cart,
.full-item .add-to-cart {
  position: absolute;
  right: 20px;
  bottom: 20px;
  background: #ca5252;
  width: 35px;
  height: 35px;
  text-align: center;
  line-height: 35px;
  color: #fff;
  border-radius: 50%;
  cursor: pointer;
  font-weight: 600px;
  transition: 300ms ease-in-out;
}
.full-item {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgb(0, 0, 0, 0.8);
  z-index: 2;
  overflow: auto;
}

.full-item > div {
  width: 600px;
  position: relative;
  margin: 10% auto;
  padding: 40px 30px;
  background: #fff;
  border-radius: 9px;
}
main .add-to-cart:hover,
.full-item .add-to-cart:hover {
  transform: scale(1.5) translateY(-5px);
}

footer {
  text-align: center;
  margin-top: 100px;
}
